*, *::after, *::before{
    box-sizing: border-box;
}
:root{
    --color-primary: #2584ff;
    --color-secondary: #00d9ff;
    --color-accent: #ff3400;
    --color-headings : #1b0760;
    --color-body: #918ca4;
    --color-body-darker:#5c5577;
    --color-border:#ccc;
    --border-radius: 30px;
}

/* Topography */
::selection{
    background: var(--color-primary);
    color: white;
}

html{
    font-size: 62.5%;
}

body{
    font-family: "Inter", sans-serif;
    color: var(--color-body);
    font-size: 2.4rem;
    line-height: 1.5;
}

h1,h2,h3{
    color: var(--color-headings);
    margin-bottom: 1rem;
    line-height: 1.1;
}

h1{
    font-size: 7rem;
}

h2{
    font-size: 4rem;
}

h3{
    font-size: 3rem;
    
}

p{
    margin-top: 0;
}

@media screen and (max-width:1024px){
    body{
        font-size: 1.8rem;
    }
    h1{
        font-size: 8rem;
    }
    h3{
        font-size: 2.4rem;
    }

}


/* Links */
a{
    text-decoration: none;
}
.link-arrow{
    color: var(--color-accent);
    text-transform: uppercase;
    font-size: 2rem;
    font-weight: bold;
}

.link-arrow::after{
    content: "-->";
    margin-left: 5px;
    transition: margin 0.3s ease;
}

.link-arrow:hover::after{
    margin-left: 10px;
}

@media screen and (min-width:1024px){
    .link-arrow{
        font-size: 1.5rem;
    }
}

/* Badges */
.badge{
    border-radius: 20px;
    padding: 0.5rem 2rem;
    /* font-size: 2rem; */
    font-weight: 600;
    white-space: nowrap;
}

.badge--primary{
    background: var(--color-primary);
    color: #fff;
}

.badge--secondary{
    background: var(--color-secondary);
    color: #fff;
}

.badge--small{
    font-size: 1.6rem;
    padding: 0.5rem 1.5rem;
}

@media  screen and (min-width: 1024px){
    .badge{
        font-size: 1.5rem;
    }
    .badge--small{
        font-size: 1.2rem;
    }
    
}

/* List */
.list{
    list-style: none;
    padding-left: 0;
   
}

.list--inline .list__item{
    display: inline-block;
    margin-right: 2rem;

}

.list--tick{
    list-style-image: url("../images/tick.svg");
    padding-left: 3rem;
}

.list--tick .list__item{
    padding-left: 0.5rem;
    margin-bottom: 1rem;
}

@media screen and (min-width: 1024px){
    .list--tick .list__item{
        padding-left: 0;
    }
}

/* Icons */

.icon{
    width: 40px;
    height: 40px;
}

.icon--primary{
    fill: var(--color-primary);
}

.icon--white{
    fill: white;
}

.icon--media{
    fill: #2584ff;
}

.icon-container{
    display: inline-block;
    background: #f3f9fa;
    width: 64px;
    height: 64px;
    border-radius: 100%;
   display: flex;
   justify-content: center;
   align-items: center;
}

.icon-container--accent{
    background: var(--color-accent);
}

.icon--small{
    width: 30px;
    height: 30px;
}

/* Buttons */
.btn{
    border-radius: 40px;
    border: none;
    color: #fff;
    cursor: pointer;
    font-size: 1.8rem;
    font-weight: 600;
    margin: 1rem 0;
    padding: 2rem 3vw;
    text-align: center;
    text-transform: uppercase;
    transition: all 0.3s ease;
    white-space: nowrap;
}

.btn .icon{
    width: 2rem;
    height: 2rem;
    margin-right: 1rem;
    vertical-align: middle;
}

.btn--accent{
    background:var(--color-accent);
}

.btn--accent:hover{
    background: #c92727;
}

.btn--secondary{
    background: var(--color-secondary);
}

.btn--secondary:hover{
    background: #00c8eb;
}


.btn--outline{
    background-color: #fff;
    border: 2px solid var(--color-headings);
    color: var(--color-headings);
   
}

.btn--outline:hover{
    background: var(--color-headings);
    color: white;
}

.btn--streched{
    padding-left: 6rem;
    padding-right: 6rem;
}

.btn--block{
    width: 100%;
    display: inline-block;
}


@media screen and (min-width: 1024px){
    .btn{
        font-size: 1.5rem;
    }

    .btn--outline{
        font-size: 1.4rem;
    }
}

/* Inputs */
.input{
    border-radius: var(--border-radius);
    border: 1px solid var(--color-border);
    color:var(--color-headings);
    font-size: 1rem;
    outline: 0;
    padding: 1.5rem 3.5rem;
}

.input::placeholder{
    color:#cdcbd7;
}

.input-group{
    border: 1px solid var(--color-border);
    border-radius: var(--border-radius);
    display: flex;
}

.input-group .input{
    border: 0;
    flex: 1;
    padding: 1.5rem 2rem;
    width: 0;
}

.input-group .btn{
    margin: 4px;
}
@media screen and (min-width: 1024px){
    .input{
        font-size: 1.5rem;
    }
}


/* Cards */
.card{
    border-radius: 7px;
    box-shadow: 0 0 20px 10px #f3f3f3;
    overflow: hidden;
}

.card__header,.card__body{
    padding: 2rem 3rem;
}

.card--primary .card__header{
    background: var(--color-primary);
    color: white;
}

.card--secondary .card__header{
    background: var(--color-secondary);
    color: white;
}

.card--primary .badge--primary{
    background: #126de4;
}

.card--secondary .badge--secondary{
    background: #02cdf1;
}

/* Plans */
.plan{
    transition: transform .3s ease-out;
}

.plan__name{
    color: white;
    margin: 0;
    font-weight: 500;
    font-size: 2.4rem;
}

.plan__price{
    font-size: 6rem;
}

.plan__billing-cycle{
    font-size: 2.4rem;
    font-weight: 300;
    opacity: .8;
    margin-right: 1rem;
}

.plan__description{
    display: block;
    font-size: 2rem;
    font-weight: 300;
    letter-spacing: 1px;
}

.plan .list__item{
    margin-bottom: 2rem;
}



.plan--popular .card__header{
    position: relative;
}

.plan--popular .card__header::before{
    content: url(../images/popular.svg);
    width: 40px;
    display: inline-block;
    position: absolute;
    top: -6px;
    right: 5%;
}

.plan:hover{
    transform: scale(1.05);
}



@media screen and (min-width: 1024px){
    .plan__name{
        font-size: 1.4rem;
    }

    .plan__price{
        font-size: 5rem;
    }

    .plan__billing-cycle{
        font-size: 1.6rem;
    }

    .plan__description{
        font-size: 1.7rem;
    }

    .plan .list__item{
        font-size: 1.8rem;
        margin-bottom: 1rem;
    }

    .plan--popular:hover{
        transform: scale(1.15);
    }

    .plan--popular{
        transform: scale(1.1);
    }
}

/* Media */
.media{
    display: flex;
    margin-bottom: 4rem;
}

.media__title{
    margin-top: 0;
    font-size: 2.4rem;
}

.media__body{
    margin: 0 2rem;
    
}

.media__image{
    margin-top: 1rem;
}

.media__message{
    font-size: 2rem;
}

@media screen and (min-width: 1024px){
    .media__title{
        font-size: 1.8rem;
    }

    .media__message{
        font-size: 1.8rem;
    }
}

/* Quote */
.quote{
    font-size: 2rem;
    font-style: italic;
    color: var(--color-body-darker);
    line-height: 1.5;
    margin: 2rem 0;
}

.quote__text::before{
    content: open-quote;
}

.quote__text::after{
    content: close-quote;
}

.quote__author{
    font-size: 3rem;
    font-weight: 500;
    font-style: normal;
    margin-bottom: 0;
}

.quote__organisation{
    color: var(--color-headings);
    opacity: .4;
    font-style: normal;
}

.quote__line{
    position: relative;
    bottom: 10px;
}

.quote > footer{
    margin-top: 2rem;
}


@media screen  and (min-width: 1024px){
    .quote{
        font-size: 2.4rem;
        line-height: 2;
    }

    .quote__author{
        font-size: 2.4rem;
    }

    .quote__organisation{
        font-size: 1.6rem;
    }
}

/* Grids */

.grid{
    display: grid;
}

@media screen and (min-width: 768px){
    .grid--1x2{
        grid-template-columns: repeat(2,1fr);
    }

    .grid--1x5{
        grid-template-columns: repeat(5,1fr);
    }
}

@media screen and (min-width: 1024px){
    .grid--1x3{
        grid-template-columns: repeat(3,1fr);
    }
   
}


/* Testimonial */
.testimonial{
    padding: 3rem;
}

.testimonial .quote{
    margin-bottom: 0;
}

.testimonial__image > img{
    width: 100%;
}

.testimonial__image{
    position: relative;
    margin: 0 1rem;
}

.testimonial__image > .icon-container{
    position: absolute; 
    z-index: 1; 
    right: -32px; 
    top: 18px;
}

@media screen and (min-width:768px){
    .testimonial .quote,
    .testimonial .quote__author{
        font-size: 2.4rem;
    }

    .testimonial .quote{
        margin-left: 6rem;
    }
}

/* Callouts */
.callout{
    padding: 3rem;
    border-radius: 5px;

}

.callout--primary{
    background: var(--color-primary);
    color: white;
}

.callout__heading{
    color: white;
    margin-top: 0;
    font-size: 3rem;
}

.callout .btn{
    justify-self: center;
    align-self: center;
}

.callout__content{
    text-align: center;
}

@media screen  and (min-width: 768px){
    .callout .grid--1x2{
        grid-template-columns: 1fr auto;
    }
    .callout__content{
        text-align: left;
    }

    .callout .btn{
        justify-self: start;
        margin: 0 2rem;
    }
}

/* Collapsibles */
.collapsible__header{
    display: flex;
    justify-content: space-between;
}

.collapsible__heading{
    margin: 0;
    font-size: 3rem;
}

.collapsible__chevron{
    transform: rotate(-90deg);
    transition: transform 0.3s ease;
}

.collapsible__content{
    max-height: 0;
    overflow: hidden;
    opacity: 0;
    transition: all 0.3s ease;
}

.collapsible--expanded .collapsible__chevron{
    transform: rotate(0deg);
}

.collapsible--expanded .collapsible__content{
    max-height: 100vh;
    opacity: 1;

}

/* Blocks */
.block{
    --padding-vertical: 6rem;
    padding: var(--padding-vertical) 2rem;
   
}

.block__heading{
    margin-top: 0;
}

.block--dark{
    background: #000;
    color: #7b858b;
}


.block--dark h1,
.block--dark h2,
.block--dark h3{
    color: white;
}


.block--skewed-right{
    padding-bottom: calc( var(--padding-vertical) + 4rem);
    clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 90%);

}

.block--skewed-left{
    padding-bottom: calc( var(--padding-vertical) + 4rem );
    clip-path: polygon(0% 0%, 100% 0%, 100% 80%, 0% 100%);

}

.block__header{
    text-align: center;
    margin-bottom: 4rem;
}

.container{
    max-width: 1140px;
    margin: 0 auto;
}

/* Navigation Bar */
.nav{
    background: #000;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0 1rem;
    align-items: center;
}

.nav__list{
    width: 100%;
    margin: 0; 
}

.nav__item{
    padding: 0.5rem 2rem;
    border-bottom: 1px solid #222;
}

.nav__item>a{
    color: #d2d0db;
    transition: color 0.3s ease;
}
.nav__item>a:hover{
    color: #fff;
}

.nav__toggler{
    opacity: .5;
    transition: all 0.3s ease;
    cursor: pointer;
}

.nav.collapsible--expanded .nav__toggler{
    opacity: 1;
    box-shadow: 0 0 0 3px #666;
    border-radius: 5px;
}

.nav__brand{
    transform: translateY(5px);
}

@media screen and (min-width: 768px){
    .nav__toggler{
        display: none;
    }

    .nav__list{
        display: flex;
        width: auto;
        font-size: 1.6rem;
        max-height: 100%;
        opacity: 1;
    }

    .nav__item{
        border: none;
    }
}

/* Hero */
.hero{
    clip-path: polygon(0% 0%, 100% 0%, 100% 90%, 0% 100%);
}
.hero__tagline{
    font-size: 2rem;
    color: #b9c3cf;
    letter-spacing: 1px;
    margin: 2rem 0 5rem;
}

.hero__image{
    width: 100%;
}

@media screen and (min-width: 768px){
    .hero{
        padding-top: 0;
    }
    .hero__content{
        text-align: left;
        align-self: center;
    }
}

/* Domain Block */
.block-domain .input-group{
    box-shadow: 0 0 30px 20px #e6ebee;
    border: 0;
    margin: 4rem auto;
    max-width: 670px;
}

.block-domain__prices{
    color: var(--color-headings);
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-template-rows: repeat(2,6rem);
    font-size: 1.5rem;
    font-weight: 600;
    justify-items: center;
    max-width: 700px;
    margin: 0 auto;
}

@media screen and (min-width: 768px){
    .block-domain__prices{
        grid-template-columns: repeat(auto-fit,minmax(10rem,1fr));
        grid-template-rows: auto;
    }
}

/* Plans block */
.block-plans .grid{
   gap: 8rem 4rem;
}

.block-plans .card{
    max-width: 500px;
    margin: 0 auto;
}

/* Footer */
.footer{
    background: #232323;
    padding-top:0;
    margin-top: 10rem;
}

.footer__heading{
    color: white;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: 600;
}

.footer__collapsible {
    border-bottom: 1px solid var(--color-border);
    padding: 2.5rem 1rem;
}

.footer__collapsible-content  a{
    color: #919191;
    text-transform: capitalize;
    transition: all 0.2s ease;
}

.footer__collapsible-content  a:hover{
    color: white;
}

.footer__collapsible-content .list__item{
    margin-bottom: 2rem;
}

.footer-brand{
    text-align: center;
    margin-top: 5rem;
}

.footer-brand > p{
    font-size: 1.5rem;
    color: #393939;
    margin-top: 10px;
}

.footer .callout{
    position: relative;
    bottom: 120px;
}

@media screen and (min-width:768px){

  .footer .grid{
    align-items: flex-start;
  }
    .footer__heading{
        font-size: 1.6rem;
    }

    .footer__collapsible{
        border: none;
    }
    .footer__collapsible-content{
        max-height: 100%;
        opacity: 1;
    }

    .footer__collapsible-content .list__item{
        font-size:1.5rem;
    }

    .footer__collapsible-header .collapsible__chevron{
        display: none;
    }

    .footer-brand{
        order: -1;
        margin-top: 20px;
        margin-right: 100px;
    }


}

/* Feature */
.feature{
    margin: 12rem 0;
}

.feature:first-of-type{
    margin-top: 6rem;
}
.feature__image{
    width: 100%;
}

.feature__title{
    margin: 1rem 0;
}

.feature.grid{
   gap: 4rem 10rem;
}


@media screen and (min-width:768px) {
    .feature{
        margin-bottom: 12rem;
    }

    .feature--reverse .feature__image{
        order: -1; 
    }
}

/* ShowCase Block*/
.showcase__image{
    width: 100%;
}

.showcase__content{
    gap: 4rem;
    margin-bottom: 4rem;
}

.showcase__media .media__message{
    font-size: 1.8rem;
    font-weight: 300;
}

.showcase__media .media__title{
    color: white;
    font-weight: 500;
}

.showcase .list{
    margin: 0;
}

@media screen and (min-width:768px){
    .showcase__media{
        margin: 2rem 0;
    }

    .showcase__media .media__title{
        font-size: 1.5rem;
    }

    .showcase__media .media__message{
        font-size: 1.5rem;
    }
}

